html,
body,
#root
{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: darkblue;
}
model-viewer {
    align-self: flex-end;
    flex-direction: column;
    width: 150px;
    height: 150px;
    border:none;
    position: absolute;
    
    animation: fly 2.3s infinite linear running;
}
@keyframes fly{
  30%{
      transform: rotateY(65deg);
      
  }
  80%{
      transform: rotateY(-65deg);
      
  }
}